 /* bring in normalize.css styles */

body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --text-color: #fbfbfb;
  --icon-active: #734aef;
  --red-color: #ff868d;
  --white-color: #fff;
  --yellow-color: #ffb627;
  --text-button: #623f8b;
  --button-color: #fcfcfc;
  --text-rgba: rgba(255, 255, 255, 0.6);
  --bg-form: rgba(255, 255, 255, 0.10);

  --button-gradient: linear-gradient(167deg, #ffc727 0%, #9e40ba 61.46%, #7000ff 90.54%);
  --dropdown: linear-gradient(360deg, rgba(83, 61, 186, 0.7) 0%, rgba(80, 48, 154, 0.7) 35.94%, rgba(106, 70, 165, 0.7) 61.04%, rgba(133, 93, 175, 0.7) 100%);

   font-family: 'Poppins', sans-serif;
    font-size: 18px;
    letter-spacing: 0.1em;
    line-height: 1.5;
    font-weight: 400;
    overflow: hidden;

    color: var(--text-color);
    background-color: black;
    background-image: url('../public/img/gradient-desktop.webp');
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;

    min-height: 100vh;

    font-synthesis: none;
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-text-size-adjust: 100%;
}
