.header {
  box-shadow: 0 4px 40px 0 rgba(0, 0, 0, 0.25),
    inset 0 -3px 2px 0 rgba(0, 0, 0, 0.1);
  background: linear-gradient(276deg, #2e1746 3.19%, #2e225f 100%);
}

.container {
  display: flex;
  justify-content: space-between;
  height: 60px;
  @media (min-width: 768px) {
    height: 80px;
  }
  @media only screen and (min-width: 1280px) {
    width: 1280px;
    margin: auto;
  }
}

.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 18px 20px;
  list-style: none;
}

@media (min-width: 768px) {
  .wrapper {
    padding: 25px 32px;
  }
}
@media (min-width: 1280px) {
  .wrapper {
    padding: 25px 16px;
  }
}

.text {
  font-size: 16px;
  color: var(--text-rgba);
}

@media (min-width: 768px) {
  .text::after {
    content: '';
    border: 0.5px solid;
    margin-left: 12px;
  }
}

.wrap {
  display: flex;
}

.link {
  display: flex;
  align-items: center;
  font-size: 16px;
  color: var(--text-rgba);
}

.btn {
  display: flex;
  background: inherit;
  padding: 0 0 0 8px;
  font-size: 16px;
  color: var(--text-rgba);
  margin-left: 10px;
  border-style: none;
}

@media (min-width: 768px) {
  .btn {
    padding: 0;
  }
}
.btn:hover,
.btn:focus {
  color: var(--text-color);
}

.exit {
  width: 18px;
  height: 18px;
  fill: var(--text-rgba);
}
@media (min-width: 768px) {
  .exit {
    margin: 0 8px 0 12px;
  }
}
.exit:hover,
.exit:focus {
  fill: var(--text-color);
}

.icon_wrap {
  padding: 12px 20px;
}
@media (min-width: 768px) {
  .icon_wrap {
    padding: 16px 32px;
  }
}
@media (min-width: 1280px) {
  .icon_wrap {
    padding: 16px;
  }
}
.icon_mob {
  width: 86px;
  height: 36px;
}
.icon_tab_desk {
  width: 115px;
  height: 48px;
}
